.table {
    display: table;
    width: flex;
    border-collapse: collapse;
    margin: 20px 20px 20px 20px;
}

.table-row {
    display: table-row;
    background: #f9f9f9;
    border: 1px solid #ddd;
}

.table-cell {
    display: table-cell;
    padding: 8px 12px;
    border: 1px solid #ddd;
}

.table-header {
    background-color: #f2f2f2;
    font-weight: bold;
}
